@import url(https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

.btn:hover {
  box-shadow: unset !important;
}

input[type="button"] {
  outline: none !important;
}

button::selection {
  outline: none !important;
}

.btn::selection {
  outline: none;
}

button:focus {
  outline: none !important;
}

:root {
  --vp: 0.052vw;
  --vm: 0.278vw;
}

.login {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  height: 100vh;
  
  background: url("https://media.istockphoto.com/vectors/abstract-black-stripes-diagonal-background-vector-id1294603953?k=20&m=1294603953&s=612x612&w=0&h=KLAV73oMUFA2ucWMMTOdXT8Vn4LVUsh6NTiXRmlX5ZA=");
  background-size: cover;
  background-repeat: no-repeat;
}

.login__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;

  background-color: #fff;
  border-radius: 10px;
  margin: 10px;
}

.login__logo {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.login__logo > img {
  max-width: 200px;
  object-fit: contain;
}

.login__desc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* margin-top: 10px; */
}

.login__desc > h3 {
  margin-top: 10px;
  text-align: center;
  font-size: xx-large;
  font-family: "Cedarville Cursive", cursive;
}
.login__desc > p {
  color: gray;
  text-align: center;
  font-weight: 500;
  font-size: 20px;
}

.login__auth {
  display: flex;
}

.login__authOptions {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 0.5 1;
  margin: 10px;
}

.login__authOption {
  display: flex;
  align-items: center;
 
  min-width: 80%;
  padding: 7px;
  margin-bottom: 15px;
  border: 1px solid lightgray;
  border-radius: 5px;
  cursor: pointer;
}

.login__authOption:hover {
  background-color: #222;
  color: #eee;
}

.login__authOption > img {
  height: 30px;
  object-fit: contain;
}



.login__authOption > p {
  margin-left: 11px;
}
.login__authOption > span {
  margin-left: 11px;
}
.login__authDesc {
  display: flex;
  align-items: center;
  padding: 7px;
  margin-bottom: 15px;
}
.login__authDesc > p {
  font-size: small;
  color: gray;
  text-align: center;
}

.login__emailPass {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  flex: 0.5 1;
  margin-left: 20px;
}

.login__emailPass > h4 {
  padding: 5px;
  text-align: center;
}

.login__emailPass > p{
  text-align: center;
}

.login__emailPass > p:hover{
  color: #000;
}

.login__inputFields {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 5px;
}

.login__inputField {
  padding: 5px;
  display: flex;
  width: 100%;
  align-items: center;
}

.login__inputField > input {
  padding: 10px;
  width: 100%;
  outline: none;
  border: none;
  border: 1px solid lightgray;
  background-color: rgb(236, 236, 236);
  border-radius: 3px;
}

.login__inputField > input:focus {
  border: 2px solid #000;
}

.login__forgButt {
  padding-left: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.login__forgButt > small {
  color: gray;
  cursor: pointer;
}

.login__forgButt > small:hover {
  color: blue;
  text-decoration: underline;
}

.login__forgButt > button {
  padding: 9px;
  border-radius: 5px;
  border: none;
  outline: none;
  background-color: #222;
  color: white;
  font-weight: 500;
  font-size: 15px;
  cursor: pointer;
}
.login__forgButt > button:hover {
  background-color: #eee;
  color: #222;
}

.login__emailPass > button:hover {
  background-color: #eee;
  color: #222;
}
.login__emailPass > button {
  padding: 10px;
  border-radius: 5px;
  border: none;
  outline: none;
  width: 100%;
  background-color: #222;
  color: white;
  font-weight: 500;
  font-size: 15px;
  cursor: pointer;
  margin-top: 10px;
}

.login__emailPass > p:hover{
  color: red;
}

.login__lang {
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
  margin-top: 30px;
  padding: 10px;
}

.login__lang > p {
  color: gray;
  margin-right: 5px;
  cursor: pointer;
  font-size: 13px;
}

.login__lang > p:hover {
  color: gray;
  margin-right: 5px;
  text-decoration: underline;
}

.login__lang > .MuiSvgIcon-root {
  color: gray;
}

.login__footer {
  padding-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.login__footer > p {
  color: gray;
  font-size: small;
  cursor: pointer;
}

.login__footer > p:hover {
  text-decoration: underline;
}

@media only screen and (max-width: 600px) {
  .login__auth {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    width: 100%;
  }

  .login__authOptions {
    display: flex;
    width: 100%;
    border-right: none;
    flex: 1 1;
  }

  .login__emailPass {
    display: flex;
    width: 100%;
    flex-direction: column;
    flex: 1 1;
    margin-left: 0;
    padding: 20px;
    align-items: center;
  }

  .login__forgButt {
    display: flex;
    width: 100%;
  }

  .login__emailPass > button {
    width: 50%;
  }

  .login__label > h4 {
    text-align: center;
    font-size: 1.1rem;
  }

  .login__authDesc {
    margin-bottom: 0px;
  }
}

.quoraBox {
  display: flex;
  flex-direction: column;
  padding: 10px;
  border: 1px solid lightgray;
  background-color: white;
  border-radius: 5px;
  cursor: pointer;
  max-width: 700px;
}

.quoraBox:hover {
  border: 1px solid rgb(175, 175, 175);
}

.quoraBox__info {
  display: flex;
  align-items: center;
}

.quoraBox__info > h5 {
  color: rgb(129, 129, 129);
  font-weight: 300;
  margin-left: 10px;
}

.quoraBox__quora {
  display: flex;
  margin-top: 8px;
}

.quoraBox__quora > p {
  color: gray;
  font-weight: bold;
  font-size: large;
}

.feed {
  display: flex;
  flex-direction: column;
  flex: 0.6 1;
}

@media only screen and (max-width: 600px) {
  .feed {
    flex: 1 1;
    padding: 20px;
  }
}

.post {
  display: flex;
  flex-direction: column;
  padding: 10px;
  background-color: white;
  margin-top: 10px;
  border: 1px solid lightgray;
  border-radius: 5px;
  max-width: 700px;
  box-shadow: 0px 5px 8px -9px solid rgab(0, 0, 0, 0.5);
}

.post__info {
  display: flex;
  align-items: center;
}

.post__info > h4 {
  margin-left: 10px;
  cursor: pointer;
  font-size: 13px;
}
.post__info > h4:hover {
  text-decoration: underline;
}

.post__info > small {
  margin-left: 10px;
}

.post__body {
  display: flex;
  flex-direction: column;
}

.post__body > .post__question {
  margin-top: 10px;
  font-weight: bold;
  margin-bottom: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
  flex: 1 1;
}

.post__question > .post__btnAnswer {
  margin-left: auto;
  cursor: pointer;
  padding: 7px;
  background-color: #222;
  outline: none;
  border: none;
  color: lightgray;
  font-weight: 300;
  font-size: 14px;
  border-radius: 5px;
}

.post__btnAnswer:hover {
  color: #222;
  background: lightgray;
}

.post__question > p:hover {
  text-decoration: underline;
}

.post__body > .post__answer > p {
  margin-bottom: 10px;
}

/* .post__image {
  background: rgb(0, 140, 255);
} */

.post__body > img {
  width: 100%;
  max-height: 400px;
  object-fit: contain;
  background-color: transparent;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.post__footer {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.post__footer > .MuiSvgIcon-root {
  color: gray;
  margin-right: 40px;
  cursor: pointer;
}

.post__footerAction {
  background-color: lightgray;
  padding: 5px;
  align-items: center;
  display: flex;
  justify-content: space-around;
  border-radius: 33px;
}
.post__footerAction > .MuiSvgIcon-root {
  color: gray;
  margin-right: 40px;
  cursor: pointer;
}

.post__footerAction > .MuiSvgIcon-root:hover {
  color: rgb(0, 140, 255);
  margin-right: 40px;
}

.post__footerLeft {
  margin-left: auto;
}

.post__footerLeft > .MuiSvgIcon-root {
  color: gray;
  cursor: pointer;
  margin-left: 30px;
}

.modal__question {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 20px;
}

.modal__question > h1 {
  color: #8f1f1b;
  font-weight: 600;
  margin-bottom: 10px;
}

.modal__question > p {
  color: gray;
  font-size: small;
}

.modal__question > p > .name {
  color: black;
  font-weight: bold;
}

.modal__answer {
  display: flex;
  padding-top: 20px;
  flex: 1 1;
}

.modal__answer > textarea {
  width: 100%;
  height: 200px;
  padding: 5px;
  font-size: 15px;
  color: black;
}

.modal__button {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 50px;
  width: 100%;
}

.modal__button > .cancle {
  border: none;
  margin-top: 10px;
  outline: none;
  color: gray;
  font-weight: 500;
  padding: 10px;
  border-radius: 33px;
  cursor: pointer;
}

.modal__button > .cancle:hover {
  color: red;
}
.modal__button > .add {
  border: none;
  outline: none;
  margin-top: 5px;
  background-color: #222;
  color: white;
  font-weight: 700;
  padding: 10px;
  border-radius: 33px;
  cursor: pointer;
  width: 50%;
}
.modal__button > .add:hover {
  background-color: #eee;
  color: #222;
}

.react-responsive-modal-modal {
  /* width: 600px; */
  height: 400px;
}

.quill {
  width: 100%;
  height: 150px;
}

