  width: 90vw;
  height: 90vh;
}

.quill{
  height: 50vh;
}

.react-responsive-modal-overlay{
  background: rgb(0 0 0 / 75%);
}
.sidebarOptions {
  display: flex;
  flex-direction: column;
}
.sidebarOption {
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
}

.sidebarOption:hover {
  background: rgb(211, 211, 211);
  border-radius: 5px;
}
.sidebarOption > img {
  height: 30px;
  border-radius: 5px;
  margin-left: 20px;
}

.sidebarOption > .MuiSvgIcon-root {
  font-size: medium;
  margin-left: 20px;
}
.sidebarOption > p {
  margin-left: 10px;
  color: gray;
  font-weight: 400;
  font-size: 13px;
}

.sidebarOption > .text {
  margin-left: 23px;
  color: gray;
  font-weight: 400;
  font-size: 13px;
}

.sidebar {
  margin-right: 10px;
  flex: 0.2 1;
}

@media only screen and (max-width: 768px) {
  .sidebar {
    display: none;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .widget {
    display: block;
  }
}

.application {
  flex: 0.2 1;
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  /* padding: 10px; */
  margin-right: 20px;
  border: 1px solid lightgray;
  border-radius: 5px;
  /* max-height: 85vh; */
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: white;
}

.application__header {
  border-bottom: 1px solid lightgray;
}

.application__header > h5 {
  padding: 10px;
  color: rgb(80, 80, 80);
  font-size: 15px;
  letter-spacing: 1.1px;
}

.application__contents {
  display: flex;
  flex-direction: column;
}

@media only screen and (max-width: 600px) {
  .application {
    display: none;
  }
}
@media only screen and (min-width: 600px) and (max-width: 1024px) {
  .application {
    display: block;
  }
}

.application__contents {
  display: flex;
  cursor: pointer;
}

.application__content {
  margin: 5px;
  display: flex;
  padding: 5px;
  border-bottom: 1px solid lightgray;
}

.application__content > img {
  height: 30px;
  border-radius: 5px;
}

.application__contentTitle {
  margin-left: 10px;
}

.application__contentTitle > h5 {
  color: rgb(70, 68, 68);
}

.application__contentTitle > p {
  color: gray;
  font-size: small;
}

