}

.allUser-container > h4{
    font-weight: 600;
}

.allUser-container > p{
margin: 5px 0;
}

.allUser-content{
    display: flex;
    flex-direction: column;
    max-width: 800px;
    width: 90vw;
    /* width: 100%; */
